@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background: rgb(148, 220, 236);
  background: linear-gradient(
    0deg,
    rgba(148, 220, 236, 1) 0%,
    rgba(0, 212, 255, 1) 44%,
    rgba(10, 214, 203, 1) 100%
  );
  height: 100vh;
  display: flex;
  justify-content: center;
}

.splashpage {
  height: 100%;
  width: 100%;
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.title {
  color: #eee;
  font-size: 8vw;
  max-width: 100%;
  width: 100%;
  height: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.title span {
  max-width: 0;
  overflow: hidden;
  display: inline-block;
  vertical-align: bottom;
}

@-webkit-keyframes in {
  from {
    max-width: 0;
    opacity: 0;
  }
  90% {
    max-width: 1em;
  }
  to {
    max-width: 1em;
    opacity: 1;
  }
}

@keyframes in {
  from {
    max-width: 0;
    opacity: 0;
  }
  90% {
    max-width: 1em;
  }
  to {
    max-width: 1em;
    opacity: 1;
  }
}

.title span {
  -webkit-animation: in 0.75s;
          animation: in 0.75s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.title .first {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}
.title .second {
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.logocontainer {
  height: 50%;
  width: 100%;
  display: flex;
  flex-direction: column;
  -webkit-animation: fade-in 0.05s;
          animation: fade-in 0.05s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  opacity: 0;
}

.splash-logo {
  height: 160px;
  width: 120px;
  margin-left: auto;
  margin-right: auto;
}

.startgame {
  color: #eee;
  font-style: italic;
  font-size: 2em;
}

.spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 10%;
  margin: -60px 0 0 -60px;
  transition: all 0.6s;
  -webkit-animation: flip 2s infinite;
          animation: flip 2s infinite;
  transform-style: preserve-3d;
}

@-webkit-keyframes flip {
  25% {
    transform: rotateY(180deg);
  }

  50% {
    transform: rotateZ(180deg);
  }

  74.999% {
    transform: rotateX(179deg);
  }

  75% {
    transform: rotateX(180deg);
  }
}

@keyframes flip {
  25% {
    transform: rotateY(180deg);
  }

  50% {
    transform: rotateZ(180deg);
  }

  74.999% {
    transform: rotateX(179deg);
  }

  75% {
    transform: rotateX(180deg);
  }
}

.palette {
  /* padding: 20px; */
  height: 100%;
  max-height: 100%;
  display: flex;
  flex-flow: column wrap;
}

.letterbox {
  position: relative;
  width: 18%;
  height: 7%;
  /* height: auto; */
  font-size: 2em;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 3%;
}

.available {
  background-color: #eec795;
}

.unavailable {
  background-color: #ebd7bc;
  color: #aaa;
}

.letterbox .circle {
  position: absolute;
  top: -16px;
  right: -16px;
  width: 30px;
  height: 30px;
  border-radius: 25px;
  background: #4da6ff;
  font-size: 20px;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  text-align: left;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
}

.clueContainer {
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;
  width: calc(100% - 20px);
  display: flex;
}

.clue {
  font-size: 24px;
  width: 50%;
  text-align: center;
}

.answerContainer {
  width: 50%;
}

input[type="text"] {
  width: 80%;
  height: 100%;
  padding: 10px;
  background: #ffffff;
  border: 1px solid #aaa;
  font-size: 30px;
}

input[type="text"]:disabled {
  background: #fafafa;
  border: 0px;
  font-style: oblique;
  text-align: center;
}

.hintButton {
  margin-left: 10px;
  cursor: pointer;
}

.gameboard {
  height: 90%;
  width: 90%;
  background-color: #fafafa;
  margin: auto;
  -webkit-filter: drop-shadow(0 0 0.75rem #888);
          filter: drop-shadow(0 0 0.75rem #888);
}

.gameContainer {
  display: flex;
  height: 90%;
  width: 100%;
}

.clues {
  border-right: 2px solid #eee;
  width: 75%;
  height: 100%;
}

.letters {
  width: 25%;
}

.details {
  height: 10%;
  text-align: center;
  font-size: 30px;
  color: #333;
  border-bottom: 1px solid #eee;
}

.help {
  position: absolute;
  bottom: 5px;
  right: 5px;
  cursor: pointer;
}

.victorycard {
  height: 60%;
  width: 60%;
  background-color: #fafafa;
  margin: auto;
  -webkit-filter: drop-shadow(0 0 0.75rem #888);
          filter: drop-shadow(0 0 0.75rem #888);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.logo {
  height: 25%;
  width: 25%;
  display: block;
  margin: auto;
}

.levelselectcontainer {
  height: 100vh;
  width: 100vw;
  max-height: 100vh;
  max-width: 100vw;
  display: flex;
  align-items: center;
}

.cardsContainer {
  display: flex;
  flex-flow: row wrap;
  height: 80%;
  width: 80%;
  margin: auto;
  justify-content: center;
  align-content: center;
}

.cardContainer {
  min-width: 120px;
  min-height: 150px;
  width: 30%;
  height: 40%;
  background-color: #fafafa;
  margin: 20px;
  -webkit-filter: drop-shadow(0 0 0.75rem #888);
          filter: drop-shadow(0 0 0.75rem #888);
  perspective: 1000px;
}

.cardContainerInner {
  position: relative;
  min-width: 100%;
  min-height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.cardContainer:hover {
  -webkit-filter: drop-shadow(0 0 1.5rem #333);
          filter: drop-shadow(0 0 1.5rem #333);
  cursor: pointer;
  position: relative;
}

.cardContainer:hover .cardContainerInner {
  transform: rotateY(180deg);
}

.levelImage {
  opacity: 0.4;
  max-height: 80%;
  max-width: 80%;
  width: auto;
  height: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.cardContainer:hover .levelImage {
  opacity: 1;
}

.attributions {
  position: absolute;
  bottom: 5px;
  right: 5px;
}

.cardFront,
.cardBack {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

.cardBack {
  transition-delay: 1s;
  transform: rotateY(180deg);
}

.clueSetTitle {
  width: 100%;
  height: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  color: #444;
}

